body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{
    position: fixed;
    top: 0;
    width: 100%;
    height: 30px;
    padding: 20px;
    display: flex;
    justify-content: space-between;
}

.navbar .logo{
    position: fixed;
    left: 20px;
    width: 80px;
    cursor: pointer;
}

.navbar .avatar{
    position: fixed;
    right: 20px;
    width: 30px;
    cursor: pointer;
}
*{
  margin: 0;
  padding: 0;
}

body{
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #111;
}

.banner{
    background-size: cover;
    background-position: center;
    height: 448px;
    color: white;
}

.content{
    padding-top: 140px;
    height: 190px;
    padding-left: 15px;
}

.title{
    font-size: 2rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
    width: 40%;
}

.button{
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
    cursor: pointer;
    margin-right: 1rem;
}

.button:hover{
    color: black;
    background-color: #e6e6e6;
}

.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 360px;
}

.fade_bottom{
    height: 7.4rem;
    background-image: linear-gradient(180deg, transparent, rgba(37,37,37,.61), #111);
}
.row{
    margin-left: 20px;
    color: #fff;
}

.posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
}

.poster{
    cursor: pointer;
    max-height: 250px;
    margin-right: 10px;
}

.small-poster{
    cursor: pointer;
    max-height: 175px;
    margin-right: 10px;
}

.posters::-webkit-scrollbar{
    display: none;
}

.poster:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: 250ms;
}

.small-poster:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: 250ms;
}
